'use client';
import { useEffect, useState } from "react";
import "./home.css";

export default function HomePage() {
  const [userEmail, setUserEmail] = useState("");

  useEffect(() => {
    const storedEmail = localStorage.getItem("bingequizUser");
    if (storedEmail) {
      setUserEmail(storedEmail);
    }
  }, []);

  return (
    <div className="home-container">
      <h1>Welcome to BingeQuiz!</h1>
      {userEmail && <h2>Hello, {userEmail.split("@")[0]} ðŸ‘‹</h2>}
      <p>Let's get started with your personalized trivia experience.</p>
    </div>
  );
}
